import React from 'react';
import { useState } from 'react';
import Box from '@mui/material/Box';
import Input from '@mui/material/Input';
import { IoIosAdd } from 'react-icons/io';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { IoIosClose } from 'react-icons/io';
import { IoMdCreate } from 'react-icons/io';

const Task = () => {
  const [input, setinput] = useState({ title: '', about: '' });
  const [data, setdata] = useState([]);
  const [toggle, setToggle] = useState(true);
  const [editData, setEditData] = useState(null);
  const [popup, setPopup] = useState({
    show: false,
    id: null,
  });

  const handleOnChange = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    setinput({ ...input, [name]: value });
  };

  let { title, about } = input;
  const handleSubmit = (e, index) => {
    e.preventDefault();
    if (input.title === '') {
    } else if (input && toggle === false) {
      setdata(
        data.map((elem, index) => {
          if (index === editData) {
            return { ...elem, title: input.title, about: input.about };
          } else {
            console.log('not working 2');
          }
          return elem;
        }),
        setinput({ title: '', about: '' }),
        setToggle(true)
      );
    } else {
      setdata([...data, { title, about }]);
      setinput({ title: '', about: '' });
      console.log(input);
    }
  };

  const onDelete = (index) => {
    setPopup({
      show: true,
      index,
    });
  };

  const delDialog = () => {
    if (popup.show && popup.index) {
      const deletedData = data.filter((elem, i) => i !== popup.index);
      console.log(deletedData);
      setdata(deletedData);
      setPopup({
        show: false,
        index: null,
      });
    }
  };

  const dontDel = () => {
    setPopup({
      show: false,
      index: null,
    });
  };

  const onEdit = (index) => {
    console.log(index);
    const editedData = data.filter((elem, i) => index === i);
    setToggle(false);
    console.log(editedData);
    setinput({ title: editedData[0].title, about: editedData[0].about });
    setEditData(index);
  };

  return (
    <Box bgcolor={'black'} color={'white'} height={'800px'}>
      <Box
        bgcolor={'#242320'}
        border={'2px solid #A35709'}
        borderRadius={'0px 0px 8px 8px'}
        width={'1398px'}
        height={'82px'}
        mb={'5px'}
        style={{ filter: popup.show ? 'blur(3px)' : 'none' }}
      >
        <Typography variant='h5' margin={'17px 0px 0px 45px'}>
          GYIZER
        </Typography>
        <Typography variant='body2' ml={'48px'}>
          Todo App
        </Typography>
      </Box>
      <form>
        <Box
          display={'flex'}
          margin={'44px 0px 44px 350px'}
          style={{ filter: popup.show ? 'blur(3px)' : 'none' }}
        >
          <Box mr={'5px'}>
            <Box
              bgcolor={'#242320'}
              border={'1px solid #FF8303'}
              borderRadius={'4px'}
              width={'466px'}
              height={'32px'}
              mb={'5px'}
            >
              <Input
                id='title'
                name='title'
                value={input.title}
                onChange={handleOnChange}
                placeholder='Title'
                style={{ color: 'white' }}
              />
              <br />
            </Box>

            <Box
              bgcolor={'#242320'}
              border={'1px solid #FF8303'}
              borderRadius={'4px'}
              width={'466px'}
              height={'32px'}
            >
              <Input
                id='about'
                name='about'
                value={input.about}
                onChange={handleOnChange}
                placeholder='About'
                style={{ color: 'white' }}
              />
              <br />
            </Box>
          </Box>
          {toggle ? (
            <Box
              button
              onClick={handleSubmit}
              type='submit'
              width={'70px'}
              height={'70px'}
              bgcolor={'#242320'}
              border={'2px solid #FF8303'}
              borderRadius={'8px'}
            >
              <IoIosAdd size='4rem' color='#FF8303' />
            </Box>
          ) : (
            <Box
              button
              onClick={handleSubmit}
              type='submit'
              width={'70px'}
              height={'70px'}
              bgcolor={'#242320'}
              border={'2px solid #FF8303'}
              borderRadius={'8px'}
            >
              <Typography mt={'25px'} ml={'8px'}>
                Update
              </Typography>
            </Box>
          )}
        </Box>
      </form>
      <Grid
        container
        border={'2px solid #A35709'}
        bgcolor={'#242320'}
        color={'white'}
        width={'1100px'}
        height={'426px'}
        borderRadius={'8px'}
        ml={'125px'}
        padding={'73px'}
        mb={'30px'}
      >
        {popup.show && (
          <Box
            position={'fixed'}
            top={'50%'}
            bottom={0}
            left={'40%'}
            right={0}
            borderTop={'4px solid #A35709'}
            bgcolor={'#242320'}
            color={'white'}
            width={'281px'}
            height={'143px'}
            borderRadius={'4px'}
            zIndex={1}
          >
            <Typography mt={'37px'} ml={'80px'}>
              Delete this task?
            </Typography>
            <Box display={'flex'} mt={'41px'} ml={'70px'}>
              <Box
                button
                border={'2px solid #A35709'}
                bgcolor={'#242320'}
                color={'white'}
                width={'64px'}
                height={'24px'}
                borderRadius={'4px'}
                onClick={delDialog}
                mr={'5px'}
              >
                <Typography ml={'18px'}>Yes</Typography>
              </Box>
              <Box
                border={'2px solid #A35709'}
                bgcolor={'#242320'}
                color={'white'}
                width={'64px'}
                height={'24px'}
                borderRadius={'4px'}
                button
                onClick={dontDel}
              >
                <Typography ml={'18px'}>No</Typography>
              </Box>
            </Box>
          </Box>
        )}
        {data.map((elem, index) => {
          return (
            <Grid
              item
              border={'2px solid #A35709'}
              bgcolor={'#242320'}
              color={'white'}
              width={'300px'}
              height={'72px'}
              borderRadius={'8px'}
              mr={'16px'}
              style={{ filter: popup.show ? 'blur(3px)' : 'none' }}
            >
              <Box display={'flex'}>
                <Box>
                  <Typography
                    width={'174px'}
                    height={'26px'}
                    variant={'h6'}
                    lineHeight={'26px'}
                    pt={'16px'}
                  >
                    {elem.title}
                  </Typography>
                  <Typography variant={'subtitle2'}>{elem.about}</Typography>
                </Box>

                <Box
                  button
                  onClick={() => onDelete(index)}
                  width={'32px'}
                  height={'32px'}
                  bgcolor={'#242320'}
                  border={'1px solid #FF8303'}
                  borderRadius={'8px'}
                  ml={'45px'}
                  mt={'15px'}
                >
                  <IoIosClose size='2rem' color='#FF8303' />
                </Box>
                <Box
                  button
                  onClick={() => onEdit(index)}
                  width={'32px'}
                  height={'32px'}
                  bgcolor={'#242320'}
                  border={'1px solid #FF8303'}
                  borderRadius={'8px'}
                  ml={'5px'}
                  mt={'15px'}
                >
                  <IoMdCreate size='2rem' color='#FF8303' />
                </Box>
              </Box>
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
};

export default Task;
